﻿@font-face {
  font-family: 'SSTArabic';
  src: url('#{$font-path}/SSTArabic-Bold/SSTArabic-Bold.eot');
  src: local('?'), 
      url('#{$font-path}/SSTArabic-Bold/SSTArabic-Bold.woff') format('woff'), 
      url('#{$font-path}/SSTArabic-Bold/SSTArabic-Bold.ttf') format('truetype'), 
      url('#{$font-path}/SSTArabic-Bold/SSTArabic-Bold.svg') format('svg');
  font-weight: 700;
  font-style: normal;
}
